import {connect} from "react-redux";
import React from 'react';
import {compose} from "redux";
import {withRouter} from "react-router-dom";
import Profile from "./Profile";
import {requestProfile, setUserStatus, updateProfileStatus} from "../../redux/profileReducer";
import {getProfile, getStatus} from "../../redux/selectors/profileSelector";
import {getAuthStatus, getAuthUserId} from "../../redux/selectors/authSelector";
import {getInitialized} from "../../redux/selectors/appSelector";

class ProfileWrapper extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId
        if(!userId) {
            userId = this.props.authorizedUserId
            if(!userId) {
                this.props.history.push("/login")
            }
        }
        this.props.getProfile(userId)
        this.props.setUserStatus(userId)
    }

    render() {
        return (
            <Profile {...this.props}
                     profile={this.props.profile}
                     status={this.props.status}
                     updateProfileStatus={this.props.updateProfileStatus}
            />
        )
    }
}

const mapStateToProps = (state) => ({
    profile: getProfile(state),
    status: getStatus(state),
    isAuth: getAuthStatus(state),
    authorizedUserId: getAuthUserId(state),
    initialized: getInitialized(state),
})

const actionCreators = {
    getProfile: requestProfile,
    updateProfileStatus,
    setUserStatus,
}

export default compose(
    withRouter,
    connect(mapStateToProps, actionCreators),
)(ProfileWrapper)